import "./src/utils/webpackPublicPath";
import supportedBrowsers from "./src/utils/supportedBrowsers";

function makeXMLHttpRequest(method, url, apiKey, done) {
  const xhr = new XMLHttpRequest();
  xhr.open(method, url);
  xhr.withCredentials = true;
  xhr.setRequestHeader("X-VideoClerk-API-Key", apiKey);
  xhr.onload = () => {
    done(null, xhr.response);
  };
  xhr.onerror = () => {
    done(xhr.response);
  };
  xhr.send();
}

/* eslint-disable consistent-return */
// eslint-disable-next-line import/prefer-default-export
export const init = (selector, config, theme) => {
  if (!supportedBrowsers.test(navigator.userAgent)) {
    return;
  }

  // check if selector is html element
  if (!(selector instanceof HTMLElement))
    return new Error("Selected element is not an HTML element!");

  const setStyles = () => {
    const style = document.createElement("style");
    style.innerHTML = `
      @import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=block");
      .vclerk-pw-container {
        position: fixed;
        left: auto;
        top: auto;
        right: 40px;
        bottom: 20px;
        z-index: 1000;
        background: transparent;
        border-radius: 24px;
        padding: 0;
        flex-direction: column;
        font-family: "Inter", Arial, Helvetica, sans-serif;
        color: #0c0e1d;
        font-weight: 300;
        display: -ms-flexbox !important;
        display: -webkit-flex !important;
        display: flex !important;
      }
      .vclerk-pw-container * {
        box-sizing: border-box;
      }
      .vclerk-pw-container .vclerk-pw-button-container {
        display: -ms-flexbox;
        /* IE 10 */
        display: -webkit-flex;
        /* Safari 6 */
        display: flex;
        /* Modern browsers */
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: 4px;
        right: 12px;
        width: 140px;
        height: 140px;
        -webkit-box-shadow: 0px 0px 46px -7px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 0px 46px -7px rgba(0, 0, 0, 0.75);
        box-shadow: 0px 0px 46px -7px rgba(0, 0, 0, 0.75);
        background-color: #0c0e1d;
        z-index: 1;
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
        border-bottom-left-radius: 12px;
      }

      .vclerk-pw-container .vclerk-pw-button-container .vclerk-pw-button.vclerk-hide-button {
        display: none;
      }

      .vclerk-pw-container .vclerk-pw-button-container .vclerk-pw-button {
        display: -ms-flexbox;
        /* IE 10 */
        display: -webkit-flex;
        /* Safari 6 */
        display: flex;
        /* Modern browsers */
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
      }
      .vclerk-pw-container .vclerk-pw-button-container .vclerk-pw-button:hover {
        cursor: pointer;
      }
      .vclerk-pw-container .vclerk-pw-button-container .vclerk-pw-button .vclerk-pw-button-descr {
        max-width: 100px;
        text-align: center;
        color: #f8f8f8;
        font-size: 12px;
      }
      .vclerk-pw-container .vclerk-pw-button-container .vclerk-pw-button .vclerk-pw-button-action {
        display: -ms-flexbox;
        /* IE 10 */
        display: -webkit-flex;
        /* Safari 6 */
        display: flex;
        /* Modern browsers */
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-top: 12px;
        font-size: 12px;
        width: 100px;
        color: #beaeff;
        text-transform: uppercase;
      }

      .vclerk-spinner {
        border: 2px solid #f3f3f3;
        border-top: 2px solid #beaeff;
        border-radius: 50%;
        width: 24px;
        height: 24px;
        animation: spin 800ms linear infinite;
      }
      
      @keyframes spin {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }
    `;

    document.getElementsByTagName("head")[0].appendChild(style);
  };

  // Create initial DOM items
  // Main container
  const domElement = selector;
  const setHTML = () => {
    domElement.innerHTML = `<div id="vclerk-pw-container" class="vclerk-pw-container" style="display: none;"><div id="vclerk-pw-wrapper" class="vclerk-pw-container-wrapper"></div></div>`;
  };

  // Spinner
  const spinner = document.createElement("div");
  spinner.setAttribute("class", "vclerk-spinner");

  const setButton = () => {
    // Create a button div container element and apply it to the correct place
    const buttonWrapper = document.createElement("div");
    buttonWrapper.setAttribute("class", "vclerk-pw-button-container");

    domElement
      .getElementsByClassName("vclerk-pw-container")[0]
      .appendChild(buttonWrapper);

    const click = () => {
      if (buttonWrapper) {
        // add a spinner and hide the button
        buttonWrapper.appendChild(spinner);
        buttonWrapper
          .getElementsByClassName("vclerk-pw-button")[0]
          .classList.add("vclerk-hide-button");
      }

      const onClose = () => setButton();

      // Dynamic import
      import(/* webpackChunkName: "vclerk-widget" */ "./index.init.jsx")
        .then((module) => {
          // remove the spinner and show the normal button behind
          if (buttonWrapper) {
            domElement
              .getElementsByClassName("vclerk-pw-container")[0]
              .removeChild(buttonWrapper);
          }
          // Dynamically Initialize the widget inside vclerk
          return module.init(
            document.getElementById("vclerk-pw-wrapper"),
            config,
            theme,
            onClose
          );
        })
        .catch(() =>
          console.error(new Error("Failed to fetch vclerk main bundle. "))
        );
    };

    // Create the button and add click event listener
    const button = document.createElement("div");
    button.setAttribute("class", "vclerk-pw-button");
    button.addEventListener("click", click);
    button.innerHTML = `
      <div class="vclerk-pw-button-icon">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="72"
          height="36"
          viewBox="0 0 603.78 176.05"
        >
          <path
            fill="#beaeff"
            d="M345.75,200.09c-20.33,0-33.32-16-33.32-35.19,0-19.08,12.2-34.87,32.69-34.87,12.51,0,22.83,5.78,28.78,16.26l18.61-12.82c-9.7-16.27-25.34-25.5-46.29-25.5-36.13,0-57.09,27.69-57.09,58.65,0,29.25,18.77,55.83,54.11,55.83,25,0,43.64-13.13,52.71-35.65l-19.86-8.29C370.93,192.27,359.67,200.09,345.75,200.09ZM158.43,187.58l-27.84-77.1h-25L147.17,220h22.2l41.76-109.47h-25ZM666.49,177.1l37.85-37.54H674.15l-28.93,30.66V110.48H622.7V220h22.52V187l32.06,33h32.06ZM158,46.41v22a89.47,89.47,0,0,1,89.37,89.37h22A111.49,111.49,0,0,0,158,46.41Zm419.56,105.2V139.56h-21.9V220h22.52V178.19c0-12.19,7.66-18.92,18.14-18.92a31.37,31.37,0,0,1,10.32,1.88L609.88,140a38,38,0,0,0-11-1.56C589.55,138.47,580.79,142.38,577.51,151.61Zm-74.13-13.92c-25.18,0-41.6,18.3-41.6,42.22s16,41,41.13,41a44.74,44.74,0,0,0,33.78-14.55l-11.41-14.07a26.6,26.6,0,0,1-20,9.07c-11.57,0-18.77-6.72-20.18-14.39h54.11v-8.44C539.19,152.39,526.06,137.69,503.38,137.69Zm-18.77,33.15c1.72-9.07,8-14.7,17.21-14.7,9.85,0,15,6.41,15,14.7ZM442.7,200.72c-5.31,0-9.38-3.76-9.38-10.17V110.63H410.8V192.9c0,18.29,11.73,28,26.27,28a40,40,0,0,0,18.46-5l-5.32-17.36A15.87,15.87,0,0,1,442.7,200.72Z"
            transform="translate(-105.57 -46.41)"
          />
        </svg>
      </div>
      <div class="vclerk-pw-button-descr">
        Få hjelp av en Video Ekspeditør!
      </div>
      <div class="vclerk-pw-button-action">
        Trykk her
        <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path fill="#beaeff" d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
      </div>
  `;

    domElement
      .getElementsByClassName("vclerk-pw-button-container")[0]
      .appendChild(button);
  };

  const display = () => {
    if (typeof config.hideWidget !== "undefined") {
      if (!config.hideWidget) {
        makeXMLHttpRequest(
          "GET",
          // eslint-disable-next-line no-undef
          `${VCLERK_ENDPOINT}/${config.queue}/available`,
          config.apiKey,
          (err, data) => {
            if (!err) {
              const json = JSON.parse(data);
              if (json.show === true) {
                setStyles();
                setHTML();
                setButton();
              }
            }
          }
        );
      }
    }
  };

  window.onload = () => {
    // Determine if we should show the button
    display();
  };
};
